import React, { useState } from 'react';
import showdown from './libraries/showdown';
import postsJson from './posts.json';
import TiLink from './components/TiLink';
import NextPrevWrapper from './components/NextPrevWrapper'

const maxPaginationItems = 20;
const allMarkdowns = [];
let paginationIndex = 0;
let wasFetched = false;

function sortComponentsByDate(components) {
  return components.sort((a, b) =>
    new Date(b.props.date).getTime()
    -
    new Date(a.props.date).getTime()
  );
}

function fetchContent(setMarkdowns, converter) {

  if(!wasFetched) {
    // this is to make sure that this fetch wil be called just once
    wasFetched = true;
    postsJson.forEach(({author, posts}) => {
      posts.forEach(({title, description, date}, i) => {
  
        fetch(`./posts/${author}/${title}.md`)
        .then(response => response.text())
        .then((md) => {
          const mdHtml = converter.makeHtml(md);

          allMarkdowns.push(
            <TiLink 
              date={date}
              key={i}
              md={mdHtml}
              author={author}
              title={title}
              description={description}
            />
          );
          setMarkdowns(sortComponentsByDate([...allMarkdowns]));
        });
      });
    });
  }
}

function changePage(direction = 0, setMarkdowns) {
  const allMarkdownsCopy = [...allMarkdowns];
  paginationIndex += direction;
  if (paginationIndex < 0) paginationIndex = 0;
  else if (paginationIndex * maxPaginationItems > allMarkdowns.length - 1) paginationIndex -= direction;
  else setMarkdowns(
    sortComponentsByDate(allMarkdownsCopy)
    .splice(paginationIndex * maxPaginationItems, maxPaginationItems)
  );
}

function App() {
  const [markdowns, setMarkdowns] = useState([]);
  const converter = new showdown.Converter();

  fetchContent(setMarkdowns, converter);

  return (
    <div>
      {
        allMarkdowns.length < maxPaginationItems ?
        []
        :
        <NextPrevWrapper
        changePage={changePage}
        setMarkdowns={setMarkdowns}
        paginationIndex={paginationIndex}
        maxPaginationItems={maxPaginationItems}
        allMarkdowns={allMarkdowns} />
      }
      {markdowns}
    </div>
  );
}

export default App;



