import React, { useState } from 'react';
import Til from './Til';

export default (props) => {
    const [hide, setHide] = useState(true);

    return (
        <div data-custom-date={props.date}>
            { 
                hide ? 
                <button onClick={() => setHide(!hide)}>
                    <h3>{props.title}</h3>
                    <i>{props.date}</i>
                    <b>made by {props.author}</b>
                    <p>{props.description}</p>
                </button>
                :
                <Til {...props}>
                    <button onClick={() => setHide(!hide)}>Contraer contenido</button>
                </Til>
            }
        </div>
    );
}



