import React from 'react';

export default ({changePage, setMarkdowns, paginationIndex, maxPaginationItems, allMarkdowns}) =>

<div className="next-prev-wrapper">
    {
        !paginationIndex ?
        []
        :
        <button onClick={()=> changePage(-1, setMarkdowns)}>Previous</button>
    }
    <span>{paginationIndex + 1}</span>
    {
        (paginationIndex + 1) * maxPaginationItems > allMarkdowns.length - 1 ?
        []
        :
        <button onClick={()=> changePage(+1, setMarkdowns)}>Next</button>
    }
</div>


